.ranking-section {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1em;
  background: var(--secondary-purple);
  color: white;
  /* padding: 2em 3em; */
  padding: 2vw 3vw;
  border-radius: 0.7em;
  box-shadow: 0 0 15px rgba(0, 0, 0, 1);
}

.ranking-section h2{
  text-align: center;
}
.ranking-button {
  align-self: center;
}
