:root {
  --main-green: #1b8d57;
  --main-purple: #002a60;
  --secondary-purple: #001b3f;
}

.app {
  position: fixed;
  display: grid;
  place-items: center;
  height: 100vh;
  width: 100%;
  overflow-y: auto;
  /* background: rgba(0, 0, 0, 0.349) url('./assets/background_optimized.png'); */
  background: repeating-conic-gradient(#001b3f 0 2%, #002a60 2% 4%);
  background-size: cover;
  background-repeat: no-repeat;
  background-blend-mode: overlay;
  background-position: center;
  padding: 2em;
}
