@import url(https://fonts.googleapis.com/css2?family=Arimo:wght@400;700&display=swap);
.game-button {
  flex-shrink: 0;
  padding: 0.8em 2em;
  border: none;
  cursor: pointer;
  color: white;
  background-color: var(--main-purple);
  font-weight: 700;
  border-radius: 0.3em;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
  -webkit-transition: -webkit-filter 0.2s;
  transition: -webkit-filter 0.2s;
  transition: filter 0.2s;
  transition: filter 0.2s, -webkit-filter 0.2s;
}

.game-button:hover,
.game-button:focus-visible {
  outline: none;
  -webkit-filter: brightness(1.3);
          filter: brightness(1.3);
}

.game-button:not([disabled]):not(.answer-button):active {
  -webkit-transform: scale(0.99);
          transform: scale(0.99);
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.3);
}

.game-button[disabled] {
  cursor: auto;
  cursor: initial;
}

.game-button[disabled]:hover {
  -webkit-filter: none;
          filter: none;
}

.input-container {
  display: flex;
  position: relative;
  width: 100%;
}

.game-input {
  padding: 0.7em;
  border: none;
  border-bottom: 1px solid gray;
  outline: none;
  width: 100%;
}

.game-input:-webkit-autofill,
.game-input:-webkit-autofill:hover, 
.game-input:-webkit-autofill:focus, 
.game-input:-webkit-autofill:active
{
 box-shadow: 0 0 0 30px white inset !important;
 background: white;
}

.game-input:focus-visible ~ .outline {
  visibility: visible;
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
}

.outline {
  content: '';
  position: absolute;
  height: 2px;
  width: 100%;
  background: var(--main-purple);
  bottom: 0;
  left: 0;
  visibility: hidden;
  -webkit-transform: scale(0, 1);
          transform: scale(0, 1);
  -webkit-transition: -webkit-transform ease-out 0.2s;
  transition: -webkit-transform ease-out 0.2s;
  transition: transform ease-out 0.2s;
  transition: transform ease-out 0.2s, -webkit-transform ease-out 0.2s;
}

.icon-section {
  display: flex;
  align-items: center;
  padding: 0 0.5em;
  color: rgb(151, 151, 151);
  border-bottom: 1px solid gray;
}

.input-section {
  flex-grow: 1;
}
.login-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  grid-gap: 3em;
  gap: 3em;
}

.login-form {
  position: relative;
  display: flex;
  justify-content: center;
  grid-gap: 2em;
  gap: 2em;
  flex-direction: column;
  background: white;
  width: 100%;
  padding: 3em 10%;
  border-radius: 0.7em;
  box-shadow: 0 0 15px rgba(0, 0, 0, 1);
}

.settings-button {
  position: absolute;
  cursor: pointer;
  font-size: 1.5em;
  color: rgb(56, 56, 56);
  right: 1em;
  top: 1em;
}

.select-container {
  display: flex;
  flex-direction: column;
  grid-gap: 0.5em;
  gap: 0.5em;
}

.select-input {
  padding: 0.8em 0.5em;
  border: none;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  background: #e4e4e4;
}

.select-input:focus-visible {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  outline: none;
}

.settings-form {
  position: relative;
  display: flex;
  justify-content: center;
  grid-gap: 2em;
  gap: 2em;
  flex-direction: column;
  background: white;
  padding: 3em;
  width: 30em;
  border-radius: 0.7em;
  box-shadow: 0 0 15px rgba(0, 0, 0, 1);
}

.select-section {
  display: grid;
  grid-gap: 2em;
  gap: 2em;
  grid-template-areas: 'category category'
                       'difficulty type';
}

.default {
  flex-wrap: wrap;
  width: 100%;
  padding: 1.2em;
}
.correct {
  box-shadow: 0 0 0 3px rgb(6, 240, 15);
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
}

.incorrect {
  box-shadow: 0 0 0 3px red;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  text-decoration: line-through;
}

.time-bar {
  width: 100%;
  height: 0.7em;
  background: -webkit-gradient(linear, left top, right top, from(red), color-stop(yellow), to(green));
  background: linear-gradient(to right, red, yellow, green);
  margin: 0 auto;
  border-radius: 30px;
  overflow: hidden;
}

.time-progress {
  width: 100%;
  height: 100%;
  border-radius: 30px;
  box-shadow: 0 0 0 1000px rgb(209, 209, 209);
  background: transparent;
}

.question {
  display: flex;
  flex-direction: column;
  grid-gap: 1em;
  gap: 1em;
  background: white;
  /* width: 70vw; */
  width: 84vw;
  padding: 2em 1em;
  border-radius: 1em;
  flex-wrap: wrap;
  box-shadow: 0 0 15px rgba(0, 0, 0, 1);
}

.question-header {
  display: flex;
  justify-content: space-between;
}

.question-text {
  padding: 1.5em 0;
  font-weight: 700;
  font-size: 1.5em;
}

.question-info {
  display: flex;
  grid-gap: 2em;
  gap: 2em;
  font-size: 0.8em;
}

.next-button {
  align-self: center;
  width: 100%;
}

.answers {
  display: flex;
  grid-gap: 1em;
  gap: 1em;
  flex-wrap: wrap;
}

@-webkit-keyframes loading {
  0% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

@keyframes loading {
  0% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

.loading-container {
  background: white;
  border-radius: 1em;
}

.loading {
  display: flex;
  flex-direction: column;
  grid-gap: 1.5em;
  gap: 1.5em;
  width: 70vw;
  height: 23em;
  background: white;
  border-radius: 1em;
  box-shadow: 0 0 15px black;
  padding: 2em 3em;
  -webkit-animation: loading linear infinite 1s alternate;
          animation: loading linear infinite 1s alternate;
}

.loading-header {
  width: 100%;
  height: 1em;
  border-radius: 5px;
  background: #cfcfcf;
}

.loading-time-bar {
  width: 100%;
  height: 0.7em;
  background: #cfcfcf;
  border-radius: 50px;
}

.loading-question {
  width: 100%;
  height: 8em;
  background: #cfcfcf;
  border-radius: 5px;
}

.loading-buttons-section {
  display: flex;
  grid-gap: 1em;
  gap: 1em;
}

.loading-button {
  width: 6em;
  height: 2.5em;
  background: #cfcfcf;
  border-radius: 5px;
}

