.feedback-section {
  position: relative;
  display: flex;
  justify-content: center;
  grid-gap: 2em;
  gap: 2em;
  flex-direction: column;
  background: white;
  /* padding: 2em 5em; */
  padding: 5% 15%;
  border-radius: 0.7em;
  /* width: 30em; */
  max-width: 30em;
  box-shadow: 0 0 15px;
  text-align: center;
}

.buttons-section {
  display: flex;
  justify-content: center;
  grid-gap: 1em;
  gap: 1em;
}

.feedback-message {
  font-weight: 700;
  font-size: 1.6em;
  margin-top: 1em;
}

.feedback-emoticon {
  font-weight: 700;
  font-size: 1.4em;
}

.success,
.failure {
  border: 4px solid black;
  border-radius: 50%;
  font-size: 4em;
}

.success {
  -webkit-filter: invert(37%) sepia(78%) saturate(5315%) hue-rotate(101deg)
    brightness(100%) contrast(102%);
          filter: invert(37%) sepia(78%) saturate(5315%) hue-rotate(101deg)
    brightness(100%) contrast(102%);
}

.failure {
  -webkit-filter: invert(21%) sepia(97%) saturate(6869%) hue-rotate(358deg)
    brightness(101%) contrast(114%);
          filter: invert(21%) sepia(97%) saturate(6869%) hue-rotate(358deg)
    brightness(101%) contrast(114%);
}

.image-container {
  width: 60%;
  aspect-ratio: 1;
  border: 4px solid var(--main-purple);
  border-radius: 50%;
  overflow: hidden;
  margin: 0 auto;
}

.pic {
  width: 100%;
}

.ranking-item {
  background: var(--main-purple);
  color: white;
  display: flex;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
  padding: 1em 1.5em;
  margin: 1em 0;
  width: 100%;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.315);
}

.ranking-item img {
  width: 3em;
  border-radius: 10px;
}

.name {
  flex-grow: 1;
}

.ranking-section {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-gap: 1em;
  gap: 1em;
  background: var(--secondary-purple);
  color: white;
  /* padding: 2em 3em; */
  padding: 2vw 3vw;
  border-radius: 0.7em;
  box-shadow: 0 0 15px rgba(0, 0, 0, 1);
}

.ranking-section h2{
  text-align: center;
}
.ranking-button {
  align-self: center;
}

:root {
  --main-green: #1b8d57;
  --main-purple: #002a60;
  --secondary-purple: #001b3f;
}

.app {
  position: fixed;
  display: grid;
  place-items: center;
  height: 100vh;
  width: 100%;
  overflow-y: auto;
  /* background: rgba(0, 0, 0, 0.349) url('./assets/background_optimized.png'); */
  background: repeating-conic-gradient(#001b3f 0 2%, #002a60 2% 4%);
  background-size: cover;
  background-repeat: no-repeat;
  background-blend-mode: overlay;
  background-position: center;
  padding: 2em;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arimo, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

